import { __ } from '@wordpress/i18n';
import {
    ButtonGroup,
	Button,
    SelectControl
} from '@wordpress/components';
import Responsive from '../responsive/responsive';
import { useDeviceType } from '../../controls/controls';

import './editor.scss';

const StybleSelectField = ({attributes, attributesKey, setAttributes, label, items, border = false, flexStyle = false, onChange = false, value = false}) => {

    // Device check fn
    const deviceType = useDeviceType();

    // Set Button value
    const setNewValue = (newValue) => {
        if(attributes?.device) {
            setAttributes({[attributesKey]: {'device': { ...attributes?.device, [deviceType]: newValue}}})
        } else{
            setAttributes({[attributesKey]: newValue})
        }
    }

    // Get active button value
    const activeValue = attributes?.device ? attributes?.device[deviceType] : attributes;

    return (
        <>
            <div className={`styble-button-group styble-component-mb ${flexStyle ? 'styble-d-flex button-style-2' : ''}`}>
                <div className='styble-component-top styble-mb-5'>
                    <label className='styble-component-title'>{label}</label>
                    {attributes?.device && <Responsive />}
                </div>

                <SelectControl
                    value={ value ? value : activeValue }
                    options={ items }
                    onChange={ ( newField ) => onChange ? onChange(newField) : setNewValue(newField) }
                    __nextHasNoMarginBottom
                />
            </div>
        </>
    )
}

export default StybleSelectField;