import { __ } from '@wordpress/i18n';
import {
    Button,
	ColorPicker,
	ColorIndicator,
	Dropdown,
    ColorPalette
} from '@wordpress/components';

import './editor.scss';


const StybleColorPicker = ( props ) => {

    const { setAttributes, value, attributesKey, label, colorType = 'normal', attributes = '', onChange } = props;

    const colorChange = (newValue) => {
        if( 'normal' === colorType ) {
            setAttributes( { [attributesKey]: newValue } );
        }
        if( 'shadowNormal' === colorType ) {
            const { horizontal, vertical, blur, spread, position, color, hoverColor } = attributes;
            setAttributes( { [attributesKey]: { horizontal, vertical, blur, spread, position, color: newValue, hoverColor } } );
        }
        if( 'shadowHover' === colorType ) {
            const { horizontal, vertical, blur, spread, position, color, hoverColor } = attributes;
            setAttributes( { [attributesKey]: { horizontal, vertical, blur, spread, position, color, hoverColor: newValue } } );
        }
        if( 'borderNormal' === colorType ) {
            const { color, hoverColor, style, width, radius } = attributes;
            setAttributes( { [attributesKey]: { color: newValue, hoverColor, style, width, radius } } );
        }
        if( 'borderHover' === colorType ) {
            const { color, hoverColor, style, width, radius } = attributes;
            setAttributes( { [attributesKey]: { color, hoverColor: newValue, style, width, radius } } );
        }
    }

    const colors = [
        { name: 'color-1', color: '#219EBC' },
        { name: 'color-2', color: '#8ECAE6' },
        { name: 'color-3', color: '#023047' },
        { name: 'color-4', color: '#FB8500' },
        { name: 'color-5', color: '#FFB703' },
    ];


   return (
       <>
        <div className='styble-color-picker styble-component-mb'>
            <p className='styble-component-title'>{label}</p>
            <Dropdown
                position="top right"
                renderToggle={ ( { isOpen, onToggle } ) => (
                    <Button
                        onClick={ onToggle }
                        aria-expanded={ isOpen }
                    >
                        <ColorIndicator colorValue={ value } />
                    </Button>
                    
                ) }
                renderContent={ () => (
                    <div>
                        <ColorPicker
                            color={ value }
                            onChange={ onChange ? onChange : colorChange }
                            colors={ colors }
                            enableAlpha
                        />
                        <ul className='styble-color-picker-palette'>
                           {colors.map((item, i) => {
                                return (
                                    <li key={i} style={{backgroundColor: item.color}}>
                                        <Button onClick={(e) => onChange ? onChange( e.target.value ) : colorChange(e.target.value) } value={item.color}></Button>
                                    </li>
                                )
                           })} 
                        </ul>
                    </div>
                )}
            />
        </div>
       </>
   )

}

export default StybleColorPicker;