import { __ } from '@wordpress/i18n';
import {
	PanelBody,
} from '@wordpress/components';
import {
    useState
} from '@wordpress/element';
import StybleButtonGroup from '../../components/buttonGroup/buttonGroup';
import TypographyNew from '../../components/typography/typography';
import Background from '../../components/background/background';
import {TransparentIcon, BgIcon, GradientIcon} from '../../components/background/svgIcon';
import StybleColorPicker from '../../components/color/color';
import Spacing from '../../components/spacing/spacing';
import Border from '../../components/border/border';
import Toggle from '../../components/toggle/toggle';
import BoxShadow from '../../components/boxShadow/boxShadow';
import StybleRanger from '../../components/rangeControl/RangeControl';
const StyleTab = ({attributes, setAttributes}) => {
    const [openTab, setOpenTab] = useState( 'content' );
    const [titleColorType, setTitleColorType] = useState( 'default' );
    const [btnColorType, setBtnColorType] = useState( 'default' );
    const [paginationColorType, setPaginationColorType] = useState( 'default' );
    const [sliderControlsColorType, setsliderControlsColorType] = useState( 'default' );
    const [captionColorType, setCaptionColorType] = useState( 'default' );
    const {
        titleTypography,
        btnTypography,
        paginationTypography,
        paginationFontSize,
        paginationFontSpacing,
        paginationLineHeight,
        titleFontSize,
        btnFontSize,
        titleFontSpacing,
        btnFontSpacing,
        titleLineHeight,
        btnLineHeight,
        titleColor,
        btnColor,
        paginationColor,
        titleMargin,
        contentTypography,
        contentFontSize,
        contentFontSpacing,
        contentLineHeight,
        contentColor,
        contentPadding,
        contentMargin,
        border,
        borderRadius,
        borderWidth,
        btnBorder,
        btnBorderRadius,
        btnBorderWidth,
        paginationBorder,
        paginationBorderRadius,
        paginationBorderWidth,
        metaColor,
        metaTypography,
        metaFontSize,
        metaFontSpacing,
        metaLineHeight,
        metaMargin,
        desMargin,
        bgColor,
        btnBgColor,
        btnPadding,
        btnMargin,
        enableBtnBoxShadow,
        enableItemBoxShadow,
        itemShadow,
        btnShadow,
        paginationBgColor,
        paginationPadding,
        paginationMargin,
        showReadMore,
        showPagination,
        showSliderControls,
        sliderControlsAlignment,
        sliderControlsColor,
        slidersControlsPadding,
        sliderControlsMargin,
        sliderControlsgap
    } = attributes;

    return(
        <>
            <PanelBody
                title={__('Content', 'styble')}
                opened={('content' === openTab || 'contentTitle' === openTab || 'contentMeta' === openTab || 'contentDes' === openTab) ? true : false} 
                onToggle={() => ('content' === openTab) ? setOpenTab('') : setOpenTab('content')}
            >
                <Background 
                    label={__('Background Color', 'styble')}
                    attributes={bgColor}
                    attributesKey={'bgColor'}
                    setAttributes={setAttributes}
                    buttonSetOption={[
                        {'label': 'Default', 'value': 'color'},
                        {'label': 'Hover', 'value': 'hover'},
                    ]}
                    items={[
                        {'label': <TransparentIcon />, 'value': 'transparent', 'tooltip': 'Transparent'},
                        {'label': <BgIcon />, 'value': 'bgColor', 'tooltip': 'Solid'},
                        {'label': <GradientIcon />, 'value': 'gradient', 'tooltip': 'Gradient'},
                    ]}
                />

                <Spacing
                    label={__('Padding', 'styble')}
                    attributes={contentPadding}
                    attributesKey={'contentPadding'}
                    setAttributes={setAttributes}
                    units={["px", '%', "em"]}
                    labelItem={{
                        'top': __('Top', 'styble'),
                        'right': __('Right', 'styble'),
                        'bottom': __('Bottom', 'styble'),
                        'left': __('Left', 'styble')
                    }}
                    defaultValue={{'unit': 'px', 'value': {
                        'top': '0',
                        'right': '12',
                        'bottom': '24',
                        'left': '12',
                    }}}
                />

                <Spacing
                    label={__('Margin', 'styble')}
                    attributes={contentMargin}
                    attributesKey={'contentMargin'}
                    setAttributes={setAttributes}
                    units={["px", '%', "em"]}
                    labelItem={{
                        'top': __('Top', 'styble'),
                        'right': __('Right', 'styble'),
                        'bottom': __('Bottom', 'styble'),
                        'left': __('Left', 'styble')
                    }}
                />

                <PanelBody
                    title={__('Title', 'styble')}
                    opened={('contentTitle' === openTab) ? true : false} 
                    onToggle={() => ('contentTitle' === openTab) ? setOpenTab('content') : setOpenTab('contentTitle')}
                >
                    <TypographyNew
                        attributes={{
                            family: titleTypography, 
                            familyKey: 'titleTypography',
                            fontSize: titleFontSize,
                            fontSizeKey: 'titleFontSize',
                            fontSpacing: titleFontSpacing,
                            fontSpacingKey: 'titleFontSpacing',
                            lineHeight: titleLineHeight,
                            lineHeightKey: 'titleLineHeight',
                        }}
                        setAttributes={ setAttributes }
                        spacingDefaultValue={{unit: 'px', value: 0}}
                        fontSizeDefault={{unit: 'px', value: 18}}
                        lineDefaultValue={{unit: 'px', value: 24}}
                    />
                    {/* Title Color */}
                    <StybleButtonGroup 
                        label={__('Color', 'styble')}
                        attributes={titleColorType}
                        items={[
                            {'label': 'Default', 'value': 'default'},
                            {'label': 'Hover', 'value': 'hover'},
                        ]}
                        onClick={(newValue) => setTitleColorType(newValue)}
                    />
                    {/* Default Icon color */}
                    {'default' === titleColorType && <StybleColorPicker
                        label={__('Color', 'styble')}
                        value={titleColor.color}
                        onChange={(newColor) => setAttributes({'titleColor': {...titleColor, 'color': newColor}})}
                    />}
                    {/* Hover Icon color */}
                    {'hover' === titleColorType && <><StybleColorPicker
                        label={__('Hover Color', 'styble')}
                        value={titleColor.hover}
                        onChange={(newColor) => setAttributes({'titleColor': {...titleColor, 'hover': newColor}})}
                    /></>}
                    <Spacing
                        label={__('Margin', 'styble')}
                        attributes={titleMargin}
                        attributesKey={'titleMargin'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '12',
                            'right': '0',
                            'bottom': '0',
                            'left': '0',
                        }}}
                    />
                </PanelBody>
                <PanelBody
                    title={__('Meta', 'styble')}
                    opened={('contentMeta' === openTab) ? true : false} 
                    onToggle={() => ('contentMeta' === openTab) ? setOpenTab('content') : setOpenTab('contentMeta')}
                >
                    <TypographyNew
                        attributes={{
                            family: metaTypography, 
                            familyKey: 'metaTypography',
                            fontSize: metaFontSize,
                            fontSizeKey: 'metaFontSize',
                            fontSpacing: metaFontSpacing,
                            fontSpacingKey: 'metaFontSpacing',
                            lineHeight: metaLineHeight,
                            lineHeightKey: 'metaLineHeight',
                        }}
                        setAttributes={ setAttributes }
                        spacingDefaultValue={{unit: 'px', value: 0}}
                        fontSizeDefault={{unit: 'px', value: 14}}
                        lineDefaultValue={{unit: 'px', value: 14}}
                    />
                    {/* content Color */}
                    <StybleButtonGroup 
                        label={__('Color', 'styble')}
                        attributes={captionColorType}
                        items={[
                            {'label': 'Default', 'value': 'default'},
                            {'label': 'Hover', 'value': 'hover'},
                        ]}
                        onClick={(newValue) => setCaptionColorType(newValue)}
                    />
                    {/* Default Icon color */}
                    {'default' === captionColorType && <StybleColorPicker
                        label={__('Color', 'styble')}
                        value={metaColor.color}
                        onChange={(newColor) => setAttributes({'metaColor': {...metaColor, 'color': newColor}})}
                    />}
                    {/* Hover Icon color */}
                    {'hover' === captionColorType && <><StybleColorPicker
                        label={__('Hover Color', 'styble')}
                        value={metaColor.hover}
                        onChange={(newColor) => setAttributes({'metaColor': {...metaColor, 'hover': newColor}})}
                    /></>}
                    <Spacing
                        label={__('Margin', 'styble')}
                        attributes={metaMargin}
                        attributesKey={'metaMargin'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '10',
                            'right': '0',
                            'bottom': '0',
                            'left': '0',
                        }}}
                    />
                </PanelBody>
                
                <PanelBody
                    title={__('Description', 'styble')}
                    opened={('contentDes' === openTab) ? true : false} 
                    onToggle={() => ('contentDes' === openTab) ? setOpenTab('content') : setOpenTab('contentDes')}
                >
                    <TypographyNew
                        attributes={{
                            family: contentTypography, 
                            familyKey: 'contentTypography',
                            fontSize: contentFontSize,
                            fontSizeKey: 'contentFontSize',
                            fontSpacing: contentFontSpacing,
                            fontSpacingKey: 'contentFontSpacing',
                            lineHeight: contentLineHeight,
                            lineHeightKey: 'contentLineHeight',
                        }}
                        setAttributes={ setAttributes }
                        spacingDefaultValue={{unit: 'px', value: 0}}
                        fontSizeDefault={{unit: 'px', value: 16}}
                        lineDefaultValue={{unit: 'px', value: 28}}
                    />
                    {/* content Color */}
                    <StybleColorPicker
                        label={__('Color', 'styble')}
                        value={contentColor.color}
                        onChange={(newColor) => setAttributes({'contentColor': {...contentColor, 'color': newColor}})}
                    />
                    <Spacing
                        label={__('Margin', 'styble')}
                        attributes={desMargin}
                        attributesKey={'desMargin'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '20',
                            'right': '0',
                            'bottom': '0',
                            'left': '0',
                        }}}
                    />
                </PanelBody>
            </PanelBody>
            {showReadMore && <PanelBody
                opened={('readMore' === openTab || 'readBorder' === openTab || 'readSpacing' === openTab || 'readBox' === openTab) ? true : false} 
                onToggle={() => ('readMore' === openTab) ? setOpenTab('') : setOpenTab('readMore')}
                title={__('Read More', 'styble')}
            >
                <Background 
                    label={__('Background Color', 'styble')}
                    attributes={btnBgColor}
                    attributesKey={'btnBgColor'}
                    setAttributes={setAttributes}
                    buttonSetOption={[
                        {'label': 'Default', 'value': 'color'},
                        {'label': 'Hover', 'value': 'hover'},
                    ]}
                    items={[
                        {'label': <TransparentIcon />, 'value': 'transparent', 'tooltip': 'Transparent'},
                        {'label': <BgIcon />, 'value': 'bgColor', 'tooltip': 'Solid'},
                        {'label': <GradientIcon />, 'value': 'gradient', 'tooltip': 'Gradient'},
                    ]}
                />
                <TypographyNew
                    attributes={{
                        family: btnTypography, 
                        familyKey: 'btnTypography',
                        fontSize: btnFontSize,
                        fontSizeKey: 'btnFontSize',
                        fontSpacing: btnFontSpacing,
                        fontSpacingKey: 'btnFontSpacing',
                        lineHeight: btnLineHeight,
                        lineHeightKey: 'btnLineHeight',
                    }}
                    setAttributes={ setAttributes }
                    spacingDefaultValue={{unit: 'px', value: 0}}
                    fontSizeDefault={{unit: 'px', value: 18}}
                    lineDefaultValue={{unit: 'px', value: 24}}
                />
                <StybleButtonGroup 
                    label={__('Color', 'styble')}
                    attributes={btnColorType}
                    items={[
                        {'label': 'Default', 'value': 'default'},
                        {'label': 'Hover', 'value': 'hover'},
                    ]}
                    onClick={(newValue) => setBtnColorType(newValue)}
                />
                {/* Default Icon color */}
                {'default' === btnColorType && <StybleColorPicker
                    label={__('Color', 'styble')}
                    value={btnColor.color}
                    onChange={(newColor) => setAttributes({'btnColor': {...btnColor, 'color': newColor}})}
                />}
                {/* Hover Icon color */}
                {'hover' === btnColorType && <><StybleColorPicker
                    label={__('Hover Color', 'styble')}
                    value={btnColor.hover}
                    onChange={(newColor) => setAttributes({'btnColor': {...btnColor, 'hover': newColor}})}
                /></>}

                <PanelBody
                    title={__('Border', 'styble')}
                    opened={('readBorder' === openTab) ? true : false} 
                    onToggle={() => ('readBorder' === openTab) ? setOpenTab('readMore') : setOpenTab('readBorder')}
                >
                    <Border 
                        attributes={ {border: btnBorder, borderWidth: btnBorderWidth, borderRadius: btnBorderRadius} }
                        attributesKey={ { border: 'btnBorder', borderWidth: 'btnBorderWidth', borderRadius: 'btnBorderRadius' } }
                        setAttributes={ setAttributes }
                        defaultValueRadius={{'unit': 'px', 'value': {
                            'top': '4',
                            'right': '4',
                            'bottom': '4',
                            'left': '4',
                        }}}
                    />
                </PanelBody>
                <PanelBody
                    title={__('Spacing', 'styble')}
                    opened={('readSpacing' === openTab) ? true : false} 
                    onToggle={() => ('readSpacing' === openTab) ? setOpenTab('readMore') : setOpenTab('readSpacing')}
                >
                    <Spacing
                        label={__('Padding', 'styble')}
                        attributes={btnPadding}
                        attributesKey={'btnPadding'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '8',
                            'right': '16',
                            'bottom': '8',
                            'left': '16',
                        }}}
                    />
                    <Spacing
                        label={__('Margin', 'styble')}
                        attributes={btnMargin}
                        attributesKey={'btnMargin'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '20',
                            'right': '0',
                            'bottom': '0',
                            'left': '0',
                        }}}
                    />
                </PanelBody>
                <PanelBody
                    title={__('Box Shadow', 'styble')}
                    opened={('readBox' === openTab) ? true : false} 
                    onToggle={() => ('readBox' === openTab) ? setOpenTab('readMore') : setOpenTab('readBox')}
                >
                    <Toggle 
                        label = {__( 'Enable Box Shadow', 'styble' )}
                        attributes= { enableBtnBoxShadow }
                        attributesKey= { 'enableBtnBoxShadow' }
                        setAttributes= { setAttributes }
                    />
                    {enableBtnBoxShadow && (
                        <BoxShadow 
                            attributes={btnShadow}
                            setAttributes={setAttributes}
                            attributesKey={'btnShadow'}
                        />

                    )}
                </PanelBody>
            </PanelBody>}
            {showPagination && <PanelBody
                opened={('pagination' === openTab || 'paginationBorder' === openTab || 'paginationSpacing' === openTab) ? true : false} 
                onToggle={() => ('pagination' === openTab) ? setOpenTab('') : setOpenTab('pagination')}
                title={__('Pagination', 'styble')}
            >
                {/* <Background 
                    label={__('Background Color', 'styble')}
                    attributes={paginationBgColor}
                    attributesKey={'paginationBgColor'}
                    setAttributes={setAttributes}
                    buttonSetOption={[
                        {'label': 'Default', 'value': 'color'},
                        {'label': 'Hover', 'value': 'hover'},
                        {'label': 'Active', 'value': 'active'},
                    ]}
                    items={[
                        {'label': <TransparentIcon />, 'value': 'transparent', 'tooltip': 'Transparent'},
                        {'label': <BgIcon />, 'value': 'bgColor', 'tooltip': 'Solid'},
                        {'label': <GradientIcon />, 'value': 'gradient', 'tooltip': 'Gradient'},
                    ]}
                /> */}
                {/* <TypographyNew
                    attributes={{
                        family: paginationTypography, 
                        familyKey: 'paginationTypography',
                        fontSize: paginationFontSize,
                        fontSizeKey: 'paginationFontSize',
                        fontSpacing: paginationFontSpacing,
                        fontSpacingKey: 'paginationFontSpacing',
                        lineHeight: paginationLineHeight,
                        lineHeightKey: 'paginationLineHeight',
                    }}
                    setAttributes={ setAttributes }
                    spacingDefaultValue={{unit: 'px', value: 0}}
                    fontSizeDefault={{unit: 'px', value: 18}}
                    lineDefaultValue={{unit: 'px', value: 24}}
                /> */}
                <StybleButtonGroup 
                    label={__('Color', 'styble')}
                    attributes={paginationColorType}
                    items={[
                        {'label': 'Default', 'value': 'default'},
                        {'label': 'Hover', 'value': 'hover'},
                        {'label': 'Active', 'value': 'active'},
                    ]}
                    onClick={(newValue) => setPaginationColorType(newValue)}
                />
                {/* Default Icon color */}
                {'default' === paginationColorType && <StybleColorPicker
                    label={__('Color', 'styble')}
                    value={paginationColor.color}
                    onChange={(newColor) => setAttributes({'paginationColor': {...paginationColor, 'color': newColor}})}
                />}
                {/* Hover Icon color */}
                {'hover' === paginationColorType && <><StybleColorPicker
                    label={__('Hover Color', 'styble')}
                    value={paginationColor.hover}
                    onChange={(newColor) => setAttributes({'paginationColor': {...paginationColor, 'hover': newColor}})}
                /></>}
                {'active' === paginationColorType && <><StybleColorPicker
                    label={__('Hover Color', 'styble')}
                    value={paginationColor.active}
                    onChange={(newColor) => setAttributes({'paginationColor': {...paginationColor, 'active': newColor}})}
                /></>}

                {/* <PanelBody
                    title={__('Border', 'styble')}
                    opened={('paginationBorder' === openTab) ? true : false} 
                    onToggle={() => ('paginationBorder' === openTab) ? setOpenTab('pagination') : setOpenTab('paginationBorder')}
                >
                    <Border 
                        attributes={ {border: paginationBorder, borderWidth: paginationBorderWidth, borderRadius: paginationBorderRadius} }
                        attributesKey={ { border: 'paginationBorder', borderWidth: 'paginationBorderWidth', borderRadius: 'paginationBorderRadius' } }
                        setAttributes={ setAttributes }
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '1',
                            'right': '1',
                            'bottom': '1',
                            'left': '1',
                        }}}
                        defaultValueRadius={{'unit': 'px', 'value': {
                            'top': '4',
                            'right': '4',
                            'bottom': '4',
                            'left': '4',
                        }}}
                    />
                </PanelBody> */}
                <PanelBody
                    title={__('Spacing', 'styble')}
                    opened={('paginationSpacing' === openTab) ? true : false} 
                    onToggle={() => ('paginationSpacing' === openTab) ? setOpenTab('pagination') : setOpenTab('paginationSpacing')}
                >
                    <Spacing
                        label={__('Padding', 'styble')}
                        attributes={paginationPadding}
                        attributesKey={'paginationPadding'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '8',
                            'right': '16',
                            'bottom': '8',
                            'left': '16',
                        }}}
                    />
                    <Spacing
                        label={__('Margin', 'styble')}
                        attributes={paginationMargin}
                        attributesKey={'paginationMargin'}
                        setAttributes={setAttributes}
                        units={["px", '%', "em"]}
                        labelItem={{
                            'top': __('Top', 'styble'),
                            'right': __('Right', 'styble'),
                            'bottom': __('Bottom', 'styble'),
                            'left': __('Left', 'styble')
                        }}
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '40',
                            'right': '4',
                            'bottom': '0',
                            'left': '0',
                        }}}
                    />
                </PanelBody>
                
            </PanelBody>}
            {showSliderControls && <PanelBody
                opened={('sliderControls' === openTab || 'sliderControlsBorder' === openTab || 'sliderControlsSpacing' === openTab) ? true : false} 
                onToggle={() => ('sliderControlssliderControls' === openTab) ? setOpenTab('sliderControls') : setOpenTab('sliderControls')}
                title={__('Sliders Controls', 'styble')}
            >
                {/* <Background 
                    label={__('Background Color', 'styble')}
                    attributes={paginationBgColor}
                    attributesKey={'paginationBgColor'}
                    setAttributes={setAttributes}
                    buttonSetOption={[
                        {'label': 'Default', 'value': 'color'},
                        {'label': 'Hover', 'value': 'hover'},
                        {'label': 'Active', 'value': 'active'},
                    ]}
                    items={[
                        {'label': <TransparentIcon />, 'value': 'transparent', 'tooltip': 'Transparent'},
                        {'label': <BgIcon />, 'value': 'bgColor', 'tooltip': 'Solid'},
                        {'label': <GradientIcon />, 'value': 'gradient', 'tooltip': 'Gradient'},
                    ]}
                /> */}
                {/* <TypographyNew
                    attributes={{
                        family: paginationTypography, 
                        familyKey: 'paginationTypography',
                        fontSize: paginationFontSize,
                        fontSizeKey: 'paginationFontSize',
                        fontSpacing: paginationFontSpacing,
                        fontSpacingKey: 'paginationFontSpacing',
                        lineHeight: paginationLineHeight,
                        lineHeightKey: 'paginationLineHeight',
                    }}
                    setAttributes={ setAttributes }
                    spacingDefaultValue={{unit: 'px', value: 0}}
                    fontSizeDefault={{unit: 'px', value: 18}}
                    lineDefaultValue={{unit: 'px', value: 24}}
                /> */}
              
                {/* Default Icon color */}
                <StybleColorPicker
                    label={__('Color', 'styble')}
                    value={sliderControlsColor.color}
                    onChange={(newColor) => setAttributes({'sliderControlsColor': {...sliderControlsColor, 'color': newColor}})}
                />
            
                {/* {'active' === sliderControlsColorType && <><StybleColorPicker
                    label={__('Hover Color', 'styble')}
                    value={paginationColor.active}
                    onChange={(newColor) => setAttributes({'paginationColor': {...paginationColor, 'active': newColor}})}
                /></>} */}

                {/* <PanelBody
                    title={__('Border', 'styble')}
                    opened={('paginationBorder' === openTab) ? true : false} 
                    onToggle={() => ('paginationBorder' === openTab) ? setOpenTab('pagination') : setOpenTab('paginationBorder')}
                >
                    <Border 
                        attributes={ {border: paginationBorder, borderWidth: paginationBorderWidth, borderRadius: paginationBorderRadius} }
                        attributesKey={ { border: 'paginationBorder', borderWidth: 'paginationBorderWidth', borderRadius: 'paginationBorderRadius' } }
                        setAttributes={ setAttributes }
                        defaultValue={{'unit': 'px', 'value': {
                            'top': '1',
                            'right': '1',
                            'bottom': '1',
                            'left': '1',
                        }}}
                        defaultValueRadius={{'unit': 'px', 'value': {
                            'top': '4',
                            'right': '4',
                            'bottom': '4',
                            'left': '4',
                        }}}
                    />
                </PanelBody> */}
                       <StybleRanger 
                label={__('Gap Between Item', 'styble')}
                setAttributes={setAttributes}
                attributes={sliderControlsgap}
                units= {false}
                attributesKey={'sliderControlsgap'}
                defaultValue={{unit: 'px'}}
            
                min='1'
            />
                
            </PanelBody>}
            <PanelBody
                opened={('border' === openTab) ? true : false} 
                onToggle={() => ('border' === openTab) ? setOpenTab('') : setOpenTab('border')}
                title={__('Border', 'styble')}
            >
                <Border 
                    attributes={ {border: border, borderWidth: borderWidth, borderRadius: borderRadius} }
                    attributesKey={ { border: 'border', borderWidth: 'borderWidth', borderRadius: 'borderRadius' } }
                    setAttributes={ setAttributes }
                    defaultValueRadius={{'unit': 'px', 'value': {
                        'top': '4',
                        'right': '4',
                        'bottom': '4',
                        'left': '4',
                    }}}
                />
            </PanelBody>
            <PanelBody
                title={__('Box Shadow', 'styble')}
                opened={('itemBoxShadow' === openTab) ? true : false} 
                onToggle={() => ('itemBoxShadow' === openTab) ? setOpenTab('') : setOpenTab('itemBoxShadow')}
            >
                <Toggle 
                    label = {__( 'Enable Box Shadow', 'styble' )}
                    attributes= { enableItemBoxShadow }
                    attributesKey= { 'enableItemBoxShadow' }
                    setAttributes= { setAttributes }
                />
                {enableItemBoxShadow && (
                    <BoxShadow 
                        attributes={itemShadow}
                        setAttributes={setAttributes}
                        attributesKey={'itemShadow'}
                    />

                )}
            </PanelBody>
        </>
    )
}

export default StyleTab;