import { __ } from '@wordpress/i18n';
import {
	PanelBody,
	__experimentalInputControl as InputControl,
} from '@wordpress/components';
import { useState } from '@wordpress/element';
import Toggle from '../../components/toggle/toggle';

const AdvancedTab = ({attributes, setAttributes}) => {
    const [openTab, setOpenTab] = useState('visibility')
    const {
        hideOnDesktop,
		hideOnTablet,
		hideOnMobile,
		additionalClass,
    } = attributes;

    return (
        <>
            <PanelBody
                title={__('Visibility Option', 'styble')}
                opened={('visibility' === openTab) ? true : false} 
                onToggle={() => ('visibility' === openTab) ? setOpenTab('') : setOpenTab('visibility')}
            >
                {/* Show hide in desktop */}
                <Toggle 
                    label = {__( 'Hide on Desktop', 'styble' )}
                    attributes= { hideOnDesktop }
                    attributesKey= { 'hideOnDesktop' }
                    setAttributes= { setAttributes }
                />
                {/* Show hide in Tablet */}
                <Toggle 
                    label = {__( 'Hide on Tablet', 'styble' )}
                    attributes= { hideOnTablet }
                    attributesKey= { 'hideOnTablet' }
                    setAttributes= { setAttributes }
                />
                {/* Show hide in Mobile */}
                <Toggle 
                    label = {__( 'Hide on Mobile', 'styble' )}
                    attributes= { hideOnMobile }
                    attributesKey= { 'hideOnMobile' }
                    setAttributes= { setAttributes }
                />
            </PanelBody>
            {/* Additional panelBody */}
            <PanelBody
                title={__('Advanced', 'styble')}
                opened={('additional' === openTab) ? true : false} 
                onToggle={() => ('additional' === openTab) ? setOpenTab('') : setOpenTab('additional')}
            >
                {/* Additional class */}
                <InputControl
                    label={__('ADDITIONAL CSS CLASS(ES)', 'styble')}
                    value={ additionalClass }
                    onChange={(newValue) => setAttributes({additionalClass: newValue})}
                />
            </PanelBody>
        </>  
    )
}

export default AdvancedTab;