import { __ } from '@wordpress/i18n';
import {
    SelectControl
} from '@wordpress/components';

import StybleColorPicker from '../color/color';
import Spacing from '../spacing/spacing';
import './editor.scss';
import { useState } from '@wordpress/element';
import StybleButtonGroup from '../buttonGroup/buttonGroup';
import StybleRanger from '../rangeControl/RangeControl';

const Border = ({attributes, attributesKey, setAttributes, defaultValue = {'unit': 'px', 'value': {
    'top': '0',
    'right': '0',
    'bottom': '0',
    'left': '0',
}}, defaultValueRadius = {'unit': 'px', 'value': {
    'top': '0',
    'right': '0',
    'bottom': '0',
    'left': '0',
}}, transition, activeColor}) => {

    const {
        border,
        borderWidth,
        borderRadius,
    } = attributes;

    const [ buttonTab, setButtonTab ] = useState('default');

    const borderColor = (newColor) => {
        setAttributes({[attributesKey.border]: {...attributes.border, 'color': newColor}})
    }

    const borderHoverColor = (newColor) => {
        setAttributes({[attributesKey.border]: {...attributes.border, 'hoverColor': newColor}})
    }

    const borderActiveColor = (newColor) => {
        setAttributes({[attributesKey.border]: {...attributes.border, 'activeColor': newColor}})
    }

    const tabButton = ( newValue ) => {
        setButtonTab( newValue )
    }

    let borderColorOptions = [
        {'label': 'Default', 'value': 'default'},
        {'label': 'Hover', 'value': 'hover'},
    ];

    if( activeColor ){
        borderColorOptions = [
            ...borderColorOptions,
            {'label': 'Active', 'value': 'active'},
        ]
    }

    return (
        <>
            <div className='styble-border'>
                <div className='styble-border-style styble-component-mb'>
                    <SelectControl
                        label={__('Style', 'styble')}
                        value={ border?.style }
                        options={ [
                            { label: 'None', value: 'none' },
                            { label: 'Solid', value: 'solid' },
                            { label: 'Dotted', value: 'dotted' },
                            { label: 'Dashed', value: 'dashed' },
                            { label: 'Double', value: 'double' },
                            { label: 'Groove', value: 'groove' },
                            { label: 'Inset', value: 'inset' },
                            { label: 'Outset', value: 'outset' },
                            { label: 'Ridge', value: 'ridge' },
                        ] }
                        onChange={ ( newStyle ) => setAttributes({ [attributesKey.border]: {...attributes.border, 'style': newStyle} }) }
                        __nextHasNoMarginBottom
                    />
                </div>

                <Spacing
                    label={__('Border Width', 'styble')}
                    attributes={borderWidth}
                    attributesKey={attributesKey.borderWidth}
                    setAttributes={setAttributes}
                    units={["px", '%', "em"]}
                    defaultValue={defaultValue}
                    labelItem={{
                        'top': __('Top', 'styble'),
                        'right': __('Right', 'styble'),
                        'bottom': __('Bottom', 'styble'),
                        'left': __('Left', 'styble')
                    }}
                />

                {/* Border Color */}
                <StybleButtonGroup 
                    label={__('Border Color', 'styble')}
                    attributes={buttonTab}
                    items={borderColorOptions}
                    onClick={tabButton}
                />

                {'default' === buttonTab && <StybleColorPicker
                    label={__('Color', 'styble')}
                    value={border?.color}
                    onChange={borderColor}
                />}
                {'hover' === buttonTab && <><StybleColorPicker
                    label={__('Hover Color', 'styble')}
                    value={border?.hoverColor}
                    onChange={borderHoverColor}
                />
                {transition && <StybleRanger 
                    label={__('Transition', 'styble')}
                    setAttributes={setAttributes}
                    attributes={transition.value}
                    units={false}
                    step={0.1}
                    min={0}
                    max={30}
                    defaultValue={{unit: 'px', value: 0.3}}
                    attributesKey={transition.key}
                />}
                </>}

                {'active' === buttonTab && <StybleColorPicker
                    label={__('Active Color', 'styble')}
                    value={border?.activeColor}
                    onChange={borderActiveColor}
                />}

                <Spacing
                    label={__('Border Radius', 'styble')}
                    attributes={borderRadius}
                    attributesKey={attributesKey.borderRadius}
                    setAttributes={setAttributes}
                    units={["px", '%', "em"]}
                    defaultValue={defaultValueRadius}
                    labelItem={{
                        'top': __('Top-L', 'styble'),
                        'right': __('Top-R', 'styble'),
                        'bottom': __('B-R', 'styble'),
                        'left': __('B-L', 'styble')
                    }}
                />

            </div>
        </>
    )
}

export default Border;