import { dateI18n, format, getSettings } from "@wordpress/date";
import { RawHTML } from "@wordpress/element";
import { calendarAlt } from "@wordpress/icons";
import { Dashicon } from "@wordpress/components";
const PostItem = (props) => {
  const {
    getPost,
    column,
    enableImage,
    showTitle,
    TitleTag,
    showAuthor,
    showDate,
    showTaxonomy,
    showContent,
    showReadMore,
    getCategories,
    dataMasonry,
    masonryElement,
    maxNumberOfWords,
    attributes,
    currentSlide,
    isEditing,
    prevSlide,
    nextSlide,
    Tag,
    generatePagination,
    setCurrentSlide
  } = props;
  const { uniqueId, showPagination, showSliderControls } = attributes;

  return (
    <div
      id={`styble-post-carousel-${uniqueId}`}
      className="styble-post-carousel"
    >
      <>
        <div
          className="styble-post-carousel-slider"
          style={{
            transform: `translateX(-${currentSlide * 100}%)`,
            transition: isEditing ? "none" : "transform 0.5s ease-in-out",
          }}
        >
          {getPost?.map((post, i) => (
            <div key={i} className="styble-post-carousel-slide">
              <div key={i} className="styble-post-carousel-slide-body">
                <div className="styble-post-carousel-image">
                  <img
                    src={
                      post
                        ? post._embedded["wp:featuredmedia"]
                          ? post._embedded["wp:featuredmedia"][0].source_url
                          : null
                        : null
                    }
                  />
                </div>
                <div className="styble-post-carousel-body">
                  {showTitle && (
                    <TitleTag className="styble-post-carousel-title">
                      {" "}
                      <a href="#">{post.title.rendered}</a>
                    </TitleTag>
                  )}
                  <div className="styble-post-carousel-meta">
                    {showAuthor && (
                      <span className="styble-post-author">
                        {" "}
                        By{" "}
                        <a href="#">
                          {post
                            ? post?._embedded?.author
                              ? post?._embedded?.author[0]?.name + " "
                              : null
                            : null}
                        </a>
                      </span>
                    )}{" "}
                    <br />
                    {showDate && (
                      <time
                        className="styble-post-date"
                        dateTime={format("c", post.date_gmt)}
                      >
                        On{" "}
                        {dateI18n(getSettings().formats.date, post.date_gmt) +
                          " "}
                      </time>
                    )}
                    {showTaxonomy && (
                      <div className="styble-post-cat">
                        {getCategories(post.categories)?.map((cat, i) => (
                          <span key={i}>
                            <a href="#">
                              {0 === i ? " " + cat.name : ", " + cat.name}
                            </a>
                          </span>
                        ))}
                      </div>
                    )}
                  </div>
                  {showContent && (
                    <RawHTML className="styble-post-carousel-content">
                      {post.excerpt.rendered
                        .split(" ")
                        .slice(0, parseInt(maxNumberOfWords.value))
                        .join(" ")}
                    </RawHTML>
                  )}
                  {showReadMore && (
                    <div className="styble-post-carousel-btn">
                      <a className="styble-post-carousel-read-more" href="#">
                        <span>Read More</span>
                      </a>
                    </div>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="styble-post-carousel-pagination-dots-button">
      
		  {  (getPost && showPagination)&&generatePagination?.map((slide, index) => (
			<button
			  key={index}
			  className={`styble-post-carousel-dot-xyz ${
				index === currentSlide ? "active" : ""
			  }`}
			  onClick={() => setCurrentSlide(index)}
			/>
		  ))}
		</div>

        <div className="styble-post-carousel-slider-controls">
      {showSliderControls &&  <>  <button
            className="styble-post-carousel-prev-button"
            onClick={prevSlide}
          >
            &lt;
          </button>
          <button
            className="styble-post-carousel-next-button"
            onClick={nextSlide}
          >
            &gt;
          </button></>}
        </div>
      </>
    </div>
  );
};

export default PostItem;
