import { __ } from "@wordpress/i18n";
import { useBlockProps, RichText } from "@wordpress/block-editor";
import "./editor.scss";
import { useEffect, useState } from "@wordpress/element";
let firstTimeLoad = true;
import { useDeviceType, createCssLink } from "../../controls/controls";
import { select, dispatch } from "@wordpress/data";
import dynamicCss from "./dynamicCss";
import Inspector from "./Inspector";
import {
  useSelect,
} from '@wordpress/data';
import PostItem from './postItem';
export default function Edit({ attributes, setAttributes, clientId, isSelected }) {
  const {  
    column,
    enableImage,
    layout,
    gapBetweenItem,
    clickEvent,
    enableContent,
    imageSize,
    metaTypography,
    titleTypography,
    contentTypography,
    titleTag,
    showTitle,
    showAuthor,
    showDate,
    showContent,
    showTaxonomy,
    showReadMore,
    isPreview,
    postPerPage,
    showPagination,
    additionalClass,
    btnTypography,
    paginationTypography,
    orderBy,
    order,
    cateList,
    maxNumberOfWords,
    
  } = attributes;
  const Tag = titleTag;
  const { uniqueId, slides } = attributes;
  const [onLoad, setOnLoad] = useState(false);
  const deviceType = useDeviceType();
  const { getBlock } = select("core/block-editor");
  const isBlockIdReserved = () => {
    const blocksClientIds =
      select("core/block-editor").getClientIdsWithDescendants();
    const allUniqueId = [];
    blocksClientIds.forEach((id) => {
      if ("undefined" !== typeof getBlock(id).attributes.uniqueId) {
        allUniqueId.push(getBlock(id).attributes.uniqueId);
      }
    });
    const set = new Set();
    if (firstTimeLoad) {
      firstTimeLoad = false;
      for (const item of allUniqueId) {
        if (set.has(item)) {
          setOnLoad(true);
        }
        set.add(item);
      }
    }
  };

  useEffect(() => {
    firstTimeLoad = true;
    setOnLoad(false);
  }, [isSelected]);
  useEffect(() => {
    setTimeout(() => {
      isBlockIdReserved();
    }, 500);

    if (!uniqueId || onLoad) {
      setAttributes({
        uniqueId: clientId,
      });
      setOnLoad(false);
    }
  }, [onLoad]);

    // useEffect(() => {
    //   createCssLink(
    //     "styble-fontawesome-css",
    //     "styble-fontawesome-css",
    //     document.getElementById("styble-fontawesome-css").getAttribute("href")
    //   );

    //   createCssLink(
    //     "styble-style.css",
    //     "styble-style.css",
    //     `${stybleLocalize.pluginUrl}assets/css/style.min.css`
    //   );
    // }, [deviceType]);
    // Set font family for frontend.
    useEffect(() => {
      setAttributes({ fontList: JSON.stringify([`${titleTypography?.typography.family}:${titleTypography?.typography.fontWeight}`, `${metaTypography?.typography.family}:${metaTypography?.typography.fontWeight}`, `${contentTypography?.typography.family}:${contentTypography?.typography.fontWeight}`, `${btnTypography?.typography.family}:${btnTypography?.typography.fontWeight}`, `${paginationTypography?.typography.family}:${paginationTypography?.typography.fontWeight}`]) });
  }, [titleTypography, metaTypography, contentTypography, btnTypography, paginationTypography])

  useEffect(() => {
    setAttributes({
      dynamicCss: JSON.stringify(
        dynamicCss(attributes, "frontend", deviceType)
      ),
    });
  }, [attributes]);
  const [currentSlide, setCurrentSlide] = useState(0);
  const [isEditing, setIsEditing] = useState(false);

 

  const handleEditStart = () => {
    setIsEditing(true);
  };

  const handleEditEnd = () => {
    setIsEditing(false);
  };
  let categories = useSelect((select) => {
        return select('core').getEntityRecords('taxonomy', 'category', {
            'per_page': -1,
        })
    }, [])
  const getCategories = (cat) => {
    let result = [];
    cat?.map((catId) => categories && categories?.filter((item) => catId === item?.id).map((catData) => {
        result.push(catData);
    }));
    return result;
}
  let cats = {}
  if(cateList && cateList.length > 0) {
      cats = {
          categories: cateList.map(item => item.value),
      }
  }

  let getPost = useSelect((select) => {
    return select('core').getEntityRecords('postType', 'post', { 
        _embed: true, 
        'per_page': postPerPage?.value,
        orderby: orderBy,
        order: order,
        ...cats,
    })
}, [postPerPage, orderBy, order, cateList]);

// Calculate the total number of slides based on the available content
const totalSlides = getPost && Math.ceil(getPost.length / column.device.Desktop);

// Function to handle next slide action
const nextSlide = () => {
  setCurrentSlide((currentSlide + 1) % totalSlides);
};

// Function to handle previous slide action
const prevSlide = () => {
  setCurrentSlide((currentSlide - 1 + totalSlides) % totalSlides);
};
const generatePagination = () => {
  const paginationItems = [];
  for (let i = 0; i < totalSlides; i++) {
    paginationItems.push(
      <button key={i} onClick={() => setCurrentSlide(i)}>
        {i + 1}
      </button>
    );
  }
  return paginationItems;
};

  return (
    <>
      <Inspector
                attributes={attributes}
                setAttributes={setAttributes}
            />
      <style>{dynamicCss(attributes, "editPage", deviceType)}</style>
      <div {...useBlockProps()}>
      <PostItem
                        getPost={getPost}
                        maxNumberOfWords={maxNumberOfWords}
                        enableImage={enableImage}
                        showTitle={showTitle}
                        TitleTag={Tag}
                        showAuthor={showAuthor}
                        showDate={showDate}
                        showTaxonomy={showTaxonomy}
                        showContent={showContent}
                        showReadMore={showReadMore}
                        attributes={attributes}
                        currentSlide={currentSlide}
                        setCurrentSlide={setCurrentSlide}
                        isEditing={isEditing}
                        prevSlide={prevSlide}
                        nextSlide={nextSlide}
                        getCategories={getCategories}
                        generatePagination={generatePagination()}
                    />
      </div>
    </>
  );
}
