import {
    TabPanel,
} from '@wordpress/components';

import { GeneralIcon, StyleIcon, AdvancedIcon } from '../../controls/svgIcon';
import './editor.scss';


const TabControls = ({attributes, setAttributes, GeneralTab = '', StyleTab = '', AdvancedTab = ''}) => {

    const Tabs = [];

    if(GeneralTab) {
        Tabs.push({
            name: 'general',
            title: <span className='styble-tab-panel-title'>{GeneralIcon()} General</span>,
            className: 'styble-general-tab',
        })
    }
    if(StyleTab) {
        Tabs.push({
            name: 'style',
            title: <span className='styble-tab-panel-title'> {StyleIcon()} Style</span>,
            className: 'styble-style-tab',
        })
    }
    if(AdvancedTab) {
        Tabs.push({
            name: 'advanced',
            title: <span className='styble-tab-panel-title'>
            {AdvancedIcon()} Advanced</span>,
            className: 'styble-advanced-tab',
        })
    }

    return (
        <TabPanel
            className="styble-tab-panel"
            activeClass="active-tab"
            tabs={ Tabs }
        >
            { ( tab ) => {
            return (
                <>
                    {
                    (tab.name === 'general') && GeneralTab && 
                        <GeneralTab 
                            attributes={attributes}
                            setAttributes={setAttributes}
                        />
                    }
                    {(tab.name === 'style') && StyleTab && 
                        <StyleTab 
                            attributes={attributes}
                            setAttributes={setAttributes}
                        />
                    }
                    {(tab.name === 'advanced') && AdvancedTab && 
                        <AdvancedTab 
                            attributes={attributes}
                            setAttributes={setAttributes}
                        />
                    }
                </>
            )
            } }  
        </TabPanel>
    )
}

export default TabControls;