import { __ } from '@wordpress/i18n';
import { BaseControl, SelectControl } from '@wordpress/components';
import { MediaUpload } from '@wordpress/block-editor';

import './editor.scss';

const MediaPicker = ( props ) => {

	const {
		backgroundImage,
		imageKey,
        setAttributes,
        imageSizeValue,
        imageSizeKey,
		slug = 'image',
		label = __( 'Image', 'styble' ),
		disableLabel = false,
		disableRemove = false,
		mediaType = 'image',
		enableImageSize = true,
		onSelect = false,
		removeImage = false,
	} = props;

     /*
	 * Event to set Image as while adding.
	 */
	const onSelectImage = ( media ) => {
		if ( ! media || ! media.url ) {
			setAttributes( { [imageKey]: null } );
			return;
		}

		// if ( ! media.type || 'image' !== media.type ) {
		// 	setAttributes( { [imageKey]: null } );
		// 	return;
		// }

		setAttributes( { [imageKey]: media } );
	};

    const imagesSizeOptions = (image) => {
		const sizeArr = [];
		for( const size in image.sizes ){
			if( image.sizes.hasOwnProperty( size ) ){
			const p = { value: size, label: size.charAt(0).toUpperCase() + size.slice(1) };
			sizeArr.push( p );
			}
		}
		return sizeArr;
	}

    /*
	 * Event to set Image as null while removing.
	 */
	const onRemoveImage = () => {
		setAttributes( { [imageKey]: '' } );
	};

	// This is used to render an icon in place of the background image when needed.
	let placeholderIcon;

	// These are the localized texts that will show on the Select / Change Button and Popup.
	let selectMediaLabel, replaceMediaLabel;

	switch ( slug ) {
		case 'video':
			selectMediaLabel = __(
				'Select Video',
				'styble-gutenberg'
			);
			replaceMediaLabel = __(
				'Change Video',
				'styble-gutenberg'
			);
			// placeholderIcon = UAGB_Block_Icons.video_placeholder;
			break;
		case 'lottie':
			selectMediaLabel = __(
				'Select Lottie Animation',
				'styble-gutenberg'
			);
			replaceMediaLabel = __(
				'Change Lottie Animation',
				'styble-gutenberg'
			);
			// placeholderIcon = UAGB_Block_Icons.lottie;
			break;
		default:
			selectMediaLabel = __(
				'Select Image',
				'styble-gutenberg'
			);
			replaceMediaLabel = __(
				'Change Image',
				'styble-gutenberg'
			);
	}

	const renderMediaUploader = ( open ) => {
		const uploadType = backgroundImage?.url ? 'replace' : 'add';
		return(
			<button
				className={ `styble-media-control__clickable styble-media-control__clickable--${ uploadType }` }
				onClick={ open }
			>
				{ ( 'add' === uploadType ) ? (
					renderButton( uploadType )
				) : (
					<div className='styble-control-label'>{ replaceMediaLabel }</div>
				) }
			</button>
		)
	};

	const renderButton = ( buttonType ) => (
		<div className={ `styble-media-control__button styble-media-control__button--${ buttonType }` }>
			<span>{'close' === buttonType ? '×' : '+'}</span>
		</div>
	);

	// This Can Be Deprecated.
	const generateBackground = ( media ) => {
		const regex = /(?:\.([^.]+))?$/;
		let mediaURL = media;
		switch ( regex.exec( String( mediaURL ) )[1] ){
			// For Lottie JSON Files.
			case 'json':
				mediaURL = '';
				break;
			// For Videos.
			case 'avi':
			case 'mpg':
			case 'mp4':
			case 'm4v':
			case 'mov':
			case 'ogv':
			case 'vtt':
			case 'wmv':
			case '3gp':
			case '3g2':
				mediaURL = '';
				break;
		}
		return mediaURL;
	}

	return (
        <>
		<BaseControl
			className="styble-media-control"
			id={ `styble-option-selector-${ slug }` }
			label={ label }
			hideLabelFromVision={ disableLabel }
		>
			<div
				className="styble-media-control__wrapper"
				style={ {
					backgroundImage: ( ! placeholderIcon && backgroundImage?.url ) && (
						`url("${ generateBackground( backgroundImage?.url ) }")`
					),
				} }
			>
				{ ('video' === mediaType && backgroundImage) && <div className='styble-media-video'>
					<i className="fa-regular fa-circle-play"></i>
				</div>}

				{ ( placeholderIcon && backgroundImage?.url ) && (
					<div className="styble-media-control__icon styble-media-control__icon--stroke">
						{ placeholderIcon }
					</div>
				) }
				<MediaUpload
					title={ selectMediaLabel }
					onSelect={ onSelect ? onSelect : onSelectImage }
					allowedTypes={ mediaType }
					value={ backgroundImage }
					render={ ( { open } ) => renderMediaUploader( open ) }
				/>
	 			{ ( ! disableRemove && backgroundImage?.url ) && (
					<button
						className='styble-media-control__clickable styble-media-control__clickable--close'
						onClick={ removeImage ? removeImage : onRemoveImage }
					>
	 					{ renderButton( 'close' ) }
	 				</button>
	 			) }
			</div>
		</BaseControl>

        { backgroundImage && enableImageSize &&
            backgroundImage.url !== 'null' &&
            backgroundImage.url !== '' && (
                <>
                    <SelectControl
                        label={__('Image Size', 'styble')}
                        value={ imageSizeValue }
                        options={ imagesSizeOptions( backgroundImage ) }
                        onChange={ ( newSize ) => setAttributes( { [imageSizeKey]: newSize } ) }
                        __nextHasNoMarginBottom
                        
                    />
                </>

            ) 
        }
        </>
	);
};

export default MediaPicker;
