import { __ } from '@wordpress/i18n';
import {
    ButtonGroup,
	Button,
} from '@wordpress/components';
import {
	__experimentalColorGradientControl as ColorGradientControl,
} from '@wordpress/block-editor';

import StybleColorPicker from '../color/color';
import StybleButtonGroup from '../buttonGroup/buttonGroup';
import StybleRanger from '../rangeControl/RangeControl';

import './editor.scss';
import { useEffect, useState } from '@wordpress/element';
import MediaPicker from '../image/image';
import StybleSelectField from '../selectField/selectField';
import { useDeviceType } from '../../controls/controls';

const Background = ({attributes, attributesKey, setAttributes, label, items, buttonSetOption, transition, colorLabel = 'Color'}) => {
    const [colorType, setColorType] = useState('color');
    const deviceType = useDeviceType();
    // Set background type
    const setBgType = (newValue) => {
        if('color' === colorType) {
            setAttributes({[attributesKey]: {...attributes, 'color': { ...attributes.color, 'style': newValue }}})
        }
        if('hover' === colorType) {
            setAttributes({[attributesKey]: {...attributes, 'hover': { ...attributes.hover, 'style': newValue } }})
        }
        if('active' === colorType) {
            setAttributes({[attributesKey]: {...attributes, 'active': { ...attributes.active, 'style': newValue } }})
        }
    }

    const onSelect = (media) => {

        if ( ! media || ! media.url ) {
			setAttributes({[attributesKey]: {...attributes, [colorType]: { ...attributes.colorType, 'image': null } }})
			return;
		}

		// if ( ! media.type || 'image' !== media.type ) {
		// 	setAttributes( { [imageKey]: null } );
		// 	return;
		// }
		setAttributes({[attributesKey]: {...attributes, [colorType]: { ...attributes[colorType], 'image': media } }})
    }

    const removeImage = () => {
        setAttributes({[attributesKey]: {...attributes, [colorType]: { ...attributes[colorType], 'image': {} } }})
    }

    useEffect(() => {
        let resetButton = document.querySelector( '.styble-background .components-circular-option-picker button' );
        if( resetButton ) {
            resetButton.innerHTML = `<svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M2.29942 2.69002C3.20916 1.67361 4.52987 1.03513 6.00004 1.03513C8.74207 1.03513 10.9649 3.25798 10.9649 6.00001C10.9649 8.74204 8.74207 10.9649 6.00004 10.9649C3.25801 10.9649 1.03516 8.74204 1.03516 6.00001H0.0351562C0.0351562 9.29432 2.70572 11.9649 6.00004 11.9649C9.29435 11.9649 11.9649 9.29432 11.9649 6.00001C11.9649 2.70569 9.29435 0.0351257 6.00004 0.0351257C4.25328 0.0351257 2.6817 0.786454 1.59142 1.98202L0.535156 0.925751V3.65819H3.2676L2.29942 2.69002Z"/>
            </svg>`;
        }
    }, [colorType, attributes])

    return (
        <>
            {buttonSetOption && 
                <StybleButtonGroup 
                    label={__('Background', 'styble')}
                    attributes={colorType}
                    items={buttonSetOption}
                    onClick={(newValue) => setColorType(newValue)}
                />
            }

            <div className='styble-background styble-component-mb'>
                {/* Background type */}
                <ButtonGroup
                    className={`styble-background-control styble-component-mb styble-d-flex`}
                    >
                    <label className='styble-component-title'>{label}</label>
                    <div className={`styble-background-left`}>
                        {items.map((item, i) => (
                            <Button className={( attributes[colorType]['style'] === item.value ) ? 'active' : '' } key={i} value={ item.value } onClick={ (e) => setBgType( e.target.closest('button').value ) }><span>{item.label}</span>{<p>{item.tooltip}</p>}</Button>
                        ))}
                    </div>
                </ButtonGroup>
                <>
                    {'transparent' === attributes[colorType]['style'] && 
                        <></>
                    }
                    {'bgColor' === attributes[colorType]['style'] && 
                        <><StybleColorPicker
                            label={__(colorLabel, 'styble')}
                            value={attributes[colorType].solidColor}
                            onChange={(newColor) => setAttributes( {[attributesKey]: {...attributes, [colorType]: {...attributes[colorType], 'solidColor': newColor} }} )}
                        />
                        {'hover' === colorType && transition && <StybleRanger 
                            label={__('Transition', 'styble')}
                            setAttributes={setAttributes}
                            attributes={transition.value}
                            units={false}
                            step={0.1}
                            min={0}
                            max={30}
                            defaultValue={{unit: 'px', value: 0.3}}
                            attributesKey={transition.key}
                        />}
                        </>
                    }
                    {'gradient' === attributes[colorType]['style'] && 
                        <ColorGradientControl
                            gradientValue={ attributes[colorType].gradient }
                            gradients={[]}                    
                            onGradientChange={ (newValue) => setAttributes( {[attributesKey]: {...attributes, [colorType]: { ...attributes[colorType], 'gradient': newValue}}})}
                        />
                    }
                    {'image' === attributes[colorType]['style'] && <>
                        <MediaPicker
                            onSelect={onSelect}
                            removeImage={removeImage}
                            enableImageSize={false}
                            backgroundImage={ attributes[colorType].image }
                        />
                        <StybleSelectField
                            label={__('Position', 'styble')}
                            value={attributes.bgPosition.device[deviceType]}
                            attributes={attributes.bgPosition}
                            onChange={(newValue) => setAttributes( {[attributesKey]: {...attributes, 'bgPosition': { ...attributes.bgPosition,'device': {...attributes.bgPosition.device, [deviceType]: newValue}}}})}
                            flexStyle={true}
                            items={[
                                {'label': 'Inherit', 'value': 'inherit'},
                                {'label': 'Bottom', 'value': 'bottom'},
                                {'label': 'Center', 'value': 'center'},
                                {'label': 'Top', 'value': 'top'},
                                {'label': 'Right', 'value': 'right'},
                                {'label': 'Left', 'value': 'left'},
                                {'label': 'Initial', 'value': 'initial'},
                            ]}
                        />
                        <StybleSelectField
                            label={__('Attachment', 'styble')}
                            value={attributes.bgAttachment.device[deviceType]}
                            attributes={attributes.bgAttachment}
                            onChange={(newValue) => setAttributes( {[attributesKey]: {...attributes, 'bgAttachment': { ...attributes.bgAttachment,'device': {...attributes.bgAttachment.device, [deviceType]: newValue}}}})}
                            flexStyle={true}
                            items={[
                                {'label': 'Default', 'value': 'default'},
                                {'label': 'Scroll', 'value': 'scroll'},
                                {'label': 'Fixed', 'value': 'fixed'},
                            ]}
                        />
                        <StybleSelectField
                            label={__('Repeat', 'styble')}
                            value={attributes.bgRepeat.device[deviceType]}
                            attributes={attributes.bgRepeat}
                            onChange={(newValue) => setAttributes( {[attributesKey]: {...attributes, 'bgRepeat': { ...attributes.bgRepeat,'device': {...attributes.bgRepeat.device, [deviceType]: newValue}}}})}
                            flexStyle={true}
                            items={[
                                {'label': 'Default', 'value': 'default'},
                                {'label': 'No Repeat', 'value': 'no-repeat'},
                                {'label': 'Repeat', 'value': 'repeat'},
                                {'label': 'Repeat-X', 'value': 'repeat-x'},
                                {'label': 'Repeat-Y', 'value': 'repeat-y'},
                            ]}
                        />
                        <StybleSelectField
                            label={__('Size', 'styble')}
                            value={attributes.bgSize.device[deviceType]}
                            attributes={attributes.bgSize}
                            onChange={(newValue) => setAttributes( {[attributesKey]: {...attributes, 'bgSize': { ...attributes.bgSize,'device': {...attributes.bgSize.device, [deviceType]: newValue}}}})}
                            flexStyle={true}
                            items={[
                                {'label': 'Default', 'value': 'default'},
                                {'label': 'Auto', 'value': 'auto'},
                                {'label': 'Cover', 'value': 'cover'},
                                {'label': 'Contain', 'value': 'contain'},
                            ]}
                        />
                    </>}
                </>
            </div>
        </>
    )
}

export default Background;