import {
  cssString,
  cssDataCheck,
  boxCss,
  unit,
  colorControls,
  gradientColorCss,
} from "../../controls/controls";

const dynamicCss = (attributes, frontend) => {
  const {
    column,
    uniqueId,
    gapBetweenItem,
    layout,
    titleTypography,
    titleFontSize,
    titleLineHeight,
    titleFontSpacing,
    titleColor,
    titleMargin,
    contentColor,
    contentFontSize,
    contentTypography,
    contentLineHeight,
    contentFontSpacing,
    contentPadding,
    contentMargin,
    contentAlignment,
    border,
    borderRadius,
    borderWidth,
    btnBorder,
    btnBorderRadius,
    btnBorderWidth,
    paginationBorder,
    paginationBorderRadius,
    paginationBorderWidth,
    metaColor,
    metaTypography,
    btnTypography,
    btnFontSize,
    btnFontSpacing,
    btnLineHeight,
    metaFontSize,
    metaFontSpacing,
    metaLineHeight,
    metaMargin,
    hideOnDesktop,
    hideOnTablet,
    hideOnMobile,
    bgColor,
    desMargin,
    btnColor,
    btnPadding,
    btnMargin,
    btnBgColor,
    btnShadow,
    enableBtnBoxShadow,
    btnAlignment,
    paginationAlignment,
    paginationColor,
    paginationPadding,
    paginationMargin,
    paginationBgColor,
    paginationTypography,
    paginationFontSize,
    paginationFontSpacing,
    paginationLineHeight,
    enableItemBoxShadow,
    itemShadow,
    sliderControlsAlignment,
    sliderControlsColor,
    slidersControlsPadding,
    sliderControlsMargin,
    sliderControlsgap,
  } = attributes;
  let backgroundColor = colorControls(
    bgColor.color.style,
    bgColor.color.solidColor,
    bgColor.color.gradient
  );
  let backgroundColorHover = colorControls(
    bgColor.hover.style,
    bgColor.hover.solidColor,
    bgColor.hover.gradient
  );

  let btnBackground = colorControls(
    btnBgColor.color.style,
    btnBgColor.color.solidColor,
    btnBgColor.color.gradient
  );
  let btnBackgroundHover = colorControls(
    btnBgColor.hover.style,
    btnBgColor.hover.solidColor,
    btnBgColor.hover.gradient
  );
  let paginationBackground = colorControls(
    paginationBgColor.color.style,
    paginationBgColor.color.solidColor,
    paginationBgColor.color.gradient
  );
  let paginationBackgroundHover = colorControls(
    paginationBgColor.hover.style,
    paginationBgColor.hover.solidColor,
    paginationBgColor.hover.gradient
  );
  let paginationBackgroundActive = colorControls(
    paginationBgColor.active.style,
    paginationBgColor.active.solidColor,
    paginationBgColor.active.gradient
  );
  let desktopCss = {
    [`#styble-post-carousel-${uniqueId}`]: {
      position: "relative",
      "justify-content": "center",
      "align-items": "center",
      overflow: "hidden",
      display: hideOnDesktop ? "none" : "block",
    },
    [`#styble-post-carousel-${uniqueId}  .styble-post-carousel-slider`]: {
      display: "flex",
      "flex-direction": "row",
      gap: column.device.Desktop != 1 ? "20px" : "0px",
      // "padding-left":
      //   gapBetweenItem.value !== 0
      //     ? cssDataCheck(gapBetweenItem.value, gapBetweenItem.unit) / 2
      //     : "0",
      // "padding-right":
      //   gapBetweenItem.value !== 0
      //     ? cssDataCheck(gapBetweenItem.value, gapBetweenItem.unit) / 2
      //     : "0",
      transition: " transform 0.5s ease-in-out",
      "max-width": "100%",
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-slider .styble-post-carousel-slide`]:
      {
        flex:
          column.device.Desktop === 3
            ? "0 0 33%"
            : column.device.Desktop === 2
            ? "0 0 50%"
            : column.device.Desktop === 1
            ? "0 0 100%"
            : column.device.Desktop === 4
            ? "0 0 25%"
            : column.device.Desktop === 5
            ? "0 0 20%"
            : column.device.Desktop === 6
            ? "0 0 17%"
            : "0 0 0%",
        "flex-direction": "column",
        // "padding-left":
        // gapBetweenItem.value !== 0
        //   ? `calc(${cssDataCheck(gapBetweenItem.value, gapBetweenItem.unit)} / 2)`
        //   : "0",
        "padding-right":
          column.device.Desktop != 1
            ? gapBetweenItem.device?.Desktop
              ? `calc(${cssDataCheck(
                  gapBetweenItem.device?.Desktop,
                  unit(gapBetweenItem, "Desktop")
                )} / 2)`
              : "0"
            : "0px",
        // "margin-left" : column.device.Desktop!=1?"10px":"0",
        overflow: "hidden",
        "max-width":
          column.device.Desktop === 3
            ? "187px"
            : column.device.Desktop === 2
            ? "290px"
            : column.device.Desktop === 1
            ? "100%"
            : column.device.Desktop === 4
            ? "135px"
            : column.device.Desktop === 5
            ? "105px"
            : column.device.Desktop === 6
            ? "83px"
            : "0px",
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-slider .styble-post-carousel-slide  .styble-post-carousel-slide-body`]:
      {
        "align-items": "center",
        "box-sizing": "border-box",
        background: backgroundColor,
        "border-color": border.color,
        "border-style": border.style,
        "border-width": cssDataCheck(
          borderWidth.device.Desktop,
          unit(borderWidth, "Desktop")
        ),
        "border-radius": cssDataCheck(
          borderRadius.device.Desktop,
          unit(borderRadius, "Desktop")
        ),
        "box-shadow": boxCss(
          enableItemBoxShadow,
          "Desktop",
          itemShadow,
          "color"
        ),
        "margin-left": "5px",
        "margin-right": "5px",
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-slider .styble-post-carousel-slide  .styble-post-carousel-slide-body:hover`]:
      {
        background:
          "gradient" !== bgColor.hover.style ? backgroundColorHover : "",
        "border-color": border.hoverColor,
        "box-shadow": boxCss(
          enableItemBoxShadow,
          "Desktop",
          itemShadow,
          "hoverColor"
        ),
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-dot-xyz:hover`]: {
      "background-color": paginationColor.hover,
      background:
        "gradient" !== paginationBgColor.hover.style
          ? paginationBackgroundHover
          : "",
      "border-color": paginationBorder.hoverColor,
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-slider-controls`]:
      {
        "margin-top": "20px",
        display: "flex",
        "justify-content": sliderControlsAlignment,
        gap: `${sliderControlsgap.value}px`,
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-prev-button`]: {
      "background-color": sliderControlsColor.color,
      color: "#fff",
      border: "none",
      cursor: "pointer",
      "font-size": "24px",
      padding: "10px 20px",
      "border-radius": "4px",
      transition: "background-color 0.3s ease",
    },
    [`#styble-post-carousel-${uniqueId}  .styble-post-carousel-next-button`]: {
      "background-color": sliderControlsColor.color,
      color: "#fff",
      border: "none",
      cursor: "pointer",
      "font-size": "24px",
      padding: "10px 20px",
      "border-radius": "4px",
      transition: "background-color 0.3s ease",
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-title`]: {
      "text-align": contentAlignment,
      "line-height": cssDataCheck(
        titleLineHeight.device.Desktop,
        unit(titleLineHeight, "Desktop")
      ),
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-title a`]: {
      "font-size": cssDataCheck(
        titleFontSize.device.Desktop,
        unit(titleFontSize, "Desktop")
      ),
      "font-style": `${titleTypography.typography.style}`,
      "font-weight": `${titleTypography.typography.fontWeight}`,
      "font-family": `${titleTypography.typography.family}`,
      "text-transform": `${titleTypography.typography.transform}`,
      "text-decoration": `${titleTypography.typography.decoration}`,
      "letter-spacing": cssDataCheck(
        titleFontSpacing.device.Desktop,
        unit(titleFontSpacing, "Desktop")
      ),
      color: titleColor.color,
      margin: cssDataCheck(
        titleMargin.device.Desktop,
        unit(titleMargin, "Desktop")
      ),
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-title a:hover`]: {
      color: titleColor.hover,
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-body`]: {
      padding: cssDataCheck(
        contentPadding.device.Desktop,
        unit(contentPadding, "Desktop")
      ),
      margin: cssDataCheck(
        contentMargin.device.Desktop,
        unit(contentMargin, "Desktop")
      ),
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-meta, #styble-post-carousel-${uniqueId} .styble-post-carousel-meta a`]:
      {
        "font-size": cssDataCheck(
          metaFontSize.device.Desktop,
          unit(btnFontSize, "Desktop")
        ),
        "font-style": `${metaTypography.typography.style}`,
        "font-weight": `${metaTypography.typography.fontWeight}`,
        "font-family": `${metaTypography.typography.family}`,
        "text-transform": `${metaTypography.typography.transform}`,
        "text-decoration": `${metaTypography.typography.decoration}`,
        "line-height": cssDataCheck(
          metaLineHeight.device.Desktop,
          unit(metaLineHeight, "Desktop")
        ),
        "letter-spacing": cssDataCheck(
          metaFontSpacing.device.Desktop,
          unit(metaFontSpacing, "Desktop")
        ),
        color: metaColor.color,
        "text-align": contentAlignment,
        margin: cssDataCheck(
          metaMargin.device.Desktop,
          unit(metaMargin, "Desktop")
        ),
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-meta a:hover`]: {
      color: metaColor.hover,
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-content`]: {
      "font-size": cssDataCheck(
        contentFontSize.device.Desktop,
        unit(contentFontSize, "Desktop")
      ),
      "font-style": `${contentTypography.typography.style}`,
      "font-weight": `${contentTypography.typography.fontWeight}`,
      "font-family": `${contentTypography.typography.family}`,
      "text-transform": `${contentTypography.typography.transform}`,
      "text-decoration": `${contentTypography.typography.decoration}`,
      "line-height": cssDataCheck(
        contentLineHeight.device.Desktop,
        unit(contentLineHeight, "Desktop")
      ),
      "letter-spacing": cssDataCheck(
        contentFontSpacing.device.Desktop,
        unit(contentFontSpacing, "Desktop")
      ),
      color: contentColor.color,
      margin: cssDataCheck(
        desMargin.device.Desktop,
        unit(desMargin, "Desktop")
      ),
      "text-align": contentAlignment,
    },
    [`#styble-post-carousel-${uniqueId}:hover .styble-post-carousel-content`]: {
      color: contentColor.hover,
    },

    [`#styble-post-carousel-${uniqueId} .editor-styles-wrapper h1,h2,h3,h4,h5,h6 `]:
      {
        "font-size": "var(--wp--preset--font-size--xx-large)",
        "line-height": "0.15",
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-btn`]: {
      "text-align": btnAlignment,
      "margin-top": "12px",
      width: column.device.Desktop === 6 ? "58px" : "",
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-read-more`]: {
      "font-size":
        column.device.Desktop === 4
          ? "13px"
          : column.device.Desktop === 5
          ? "6px"
          : column.device.Desktop === 6
          ? "5px"
          : cssDataCheck(
              btnFontSize.device.Desktop,
              unit(btnFontSize, "Desktop")
            ),
      "font-style": `${btnTypography.typography.style}`,
      "font-weight": `${btnTypography.typography.fontWeight}`,
      "font-family": `${btnTypography.typography.family}`,
      "text-transform": `${btnTypography.typography.transform}`,
      "text-decoration": `${btnTypography.typography.decoration}`,
      "line-height": cssDataCheck(
        btnLineHeight.device.Desktop,
        unit(btnLineHeight, "Desktop")
      ),
      "letter-spacing": cssDataCheck(
        btnFontSpacing.device.Desktop,
        unit(btnFontSpacing, "Desktop")
      ),
      color: btnColor.color,
      "box-shadow": boxCss(enableBtnBoxShadow, "Desktop", btnShadow, "color"),
      padding: cssDataCheck(
        btnPadding.device.Desktop,
        unit(btnPadding, "Desktop")
      ),
      margin: cssDataCheck(
        btnMargin.device.Desktop,
        unit(btnMargin, "Desktop")
      ),
      "border-color": btnBorder.color,
      "border-style": btnBorder.style,
      "border-width": cssDataCheck(
        btnBorderWidth.device.Desktop,
        unit(btnBorderWidth, "Desktop")
      ),
      "border-radius": cssDataCheck(
        btnBorderRadius.device.Desktop,
        unit(btnBorderRadius, "Desktop")
      ),
      background: btnBackground,
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-read-more:hover`]:
      {
        color: btnColor.hover,
        background:
          "gradient" !== btnBgColor.hover.style ? btnBackgroundHover : "",
        "border-color": btnBorder.hoverColor,
        "box-shadow": boxCss(
          enableBtnBoxShadow,
          "Desktop",
          btnShadow,
          "hoverColor"
        ),
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-content`]: {
      "font-size": cssDataCheck(
        contentFontSize.device.Mobile,
        unit(contentFontSize, "Mobile")
      ),
      "line-height": cssDataCheck(
        contentLineHeight.device.Mobile,
        unit(contentLineHeight, "Mobile")
      ),
      "letter-spacing": cssDataCheck(
        contentFontSpacing.device.Mobile,
        unit(contentFontSpacing, "Mobile")
      ),
      margin: cssDataCheck(desMargin.device.Mobile, unit(desMargin, "Mobile")),
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-content`]: {
      "font-size": cssDataCheck(
        contentFontSize.device.Desktop,
        unit(contentFontSize, "Desktop")
      ),
      "font-style": `${contentTypography.typography.style}`,
      "font-weight": `${contentTypography.typography.fontWeight}`,
      "font-family": `${contentTypography.typography.family}`,
      "text-transform": `${contentTypography.typography.transform}`,
      "text-decoration": `${contentTypography.typography.decoration}`,
      "line-height": cssDataCheck(
        contentLineHeight.device.Desktop,
        unit(contentLineHeight, "Desktop")
      ),
      "letter-spacing": cssDataCheck(
        contentFontSpacing.device.Desktop,
        unit(contentFontSpacing, "Desktop")
      ),
      color: contentColor.color,
      margin: cssDataCheck(
        desMargin.device.Desktop,
        unit(desMargin, "Desktop")
      ),
      "text-align": contentAlignment,
    },
    [`#styble-post-carousel-${uniqueId}:hover .styble-post-carousel-content`]: {
      color: contentColor.hover,
    },
    // [`#styble-post-carousel-${uniqueId} .styble-post-carousel-read-more`]: {
    //   "font-size": column.device.Desktop===4?"13px":"16px"
    // },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-dot-xyz`]: {
      "background-color": paginationColor.color,
      border: "2px",
      "border-radius": "50%",

      width: "20px",
      height: "20px",
      margin: "0 5px",
      cursor: "pointer",
      transition: " background-color 0.3s ease",
      margin: cssDataCheck(
        paginationMargin.device.Desktop,
        unit(paginationMargin, "Desktop")
      ),
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-dot-xyz.active`]:
      {
        "background-color": paginationColor.active,
      },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-pagination-dots-button`]:
      {
        "text-align": paginationAlignment,
        padding: cssDataCheck(
          paginationPadding.device.Desktop,
          unit(paginationPadding, "Desktop")
        ),
      },

    [`#styble-post-grid-${uniqueId} .styble-post-carousel-pagination-dots-button:not(.current):hover`]:
      {},
    [`#styble-post-grid-${uniqueId} .styble-post-carousel-pagination-dots-button.current`]:
      {
        color: paginationColor.active,
        background: paginationBackgroundActive,
      },
  };
  desktopCss = {
    ...desktopCss,
    ...gradientColorCss(
      bgColor,
      `#styble-post-carousel-${uniqueId} .styble-post-carousel-slide-body`,
      backgroundColorHover,
      desktopCss,
      `#styble-post-carousel-${uniqueId}  .styble-post-carousel-slide-body:hover`,
      ".3"
    ),
    ...gradientColorCss(
      btnBgColor,
      `#styble-post-carosuel-${uniqueId} .styble-post-carousel-btn a`,
      btnBackgroundHover,
      desktopCss,
      `#styble-post-carousel-${uniqueId} .styble-post-carousel-more:hover`,
      ".3"
    ),
    ...gradientColorCss(
      paginationBgColor,
      `#styble-post-carousel-${uniqueId} .styble-post-carousel-pagination a`,
      paginationBackgroundHover,
      desktopCss,
      `#styble-post-carousel-${uniqueId} .styble-post-carousel-pagination a:not(.current):hover`,
      ".3",
      "editPage" === frontend ? "" : "-1"
    ),
  };
  let tabletCss = {
    [`#styble-post-carousel-${uniqueId}`]: {
      display: hideOnTablet ? "none" : "block",
    },
    [`#styble-post-carousel-${uniqueId} .styble-post-carousel-slider .styble-post-carousel-slide`]:
      {
        "padding-right": "20px"
            
      },
  };
  let mobileCss = {};
  desktopCss = cssString(desktopCss);
  tabletCss =
    "" !== cssString(tabletCss)
      ? `@media only screen and (max-width: 780px) {${cssString(tabletCss)}}`
      : "";
  mobileCss =
    "" !== cssString(mobileCss)
      ? `@media only screen and (max-width: 360px){${cssString(mobileCss)}}`
      : "";

  const styling = `${desktopCss + tabletCss + mobileCss}`;

  return styling;
};

export default dynamicCss;
