import { __ } from '@wordpress/i18n';
import {
    ButtonGroup,
	Button,
} from '@wordpress/components';
import Responsive from '../../components/responsive/responsive';
import { useDeviceType } from '../../controls/controls';

import './editor.scss';

const StybleButtonGroup = ({attributes, attributesKey, setAttributes, label, items, border = false, flexStyle = false, onClick = false}) => {

    // Device check fn
    const deviceType = useDeviceType();

    // Set Button value
    const setButtonGroup = (newValue) => {
        if(attributes?.device) {
            setAttributes({[attributesKey]: {'device': { ...attributes?.device, [deviceType]: newValue}}})
        } else{
            setAttributes({[attributesKey]: newValue})
        }
    }

    // Get active button value
    const activeValue = attributes?.device ? attributes?.device[deviceType] : attributes;

    return (
        <>
           {/* Title tag */}
            <ButtonGroup
                className={`styble-button-group styble-component-mb ${flexStyle ? 'styble-d-flex button-style-2' : ''}`}
                >
                <div className='styble-component-top'>
                    <label className='styble-component-title'>{label}</label>
                    {attributes?.device && <Responsive />}
                </div>
                <div className={`styble-button-group-list ${border ? 'has-border' : ''}`}>
                    {
                        items.map((item, i) => (
                            <Button className={(activeValue === item.value) ? 'active' : ''} key={i} value={item.value} onClick={ (e) => onClick ? onClick( e.target.closest('button').value ) : setButtonGroup( e.target.closest('button').value ) }><span>{item.label}</span>{(item.tooltip) && <p>{item.tooltip}</p>}</Button>
                        ))   
                    }
                </div>
            </ButtonGroup> 
        </>
    )
}

export default StybleButtonGroup;