import Select from 'react-select';
import { __ } from '@wordpress/i18n';
import {
    SelectControl,
    Button,
} from '@wordpress/components';
import { useEffect, useState, useRef } from '@wordpress/element';
import StybleRanger from "../rangeControl/RangeControl";
import StybleButtonGroup from "../buttonGroup/buttonGroup";
import { DecorationNone, DecorationUnderline, DecorationLineThrough, DecorationOverLine, TypographyIcon } from './svgIcon';

import './editor.scss';

const fetchFonts = async () => {
    try {
        const response = await fetch('https://www.googleapis.com/webfonts/v1/webfonts?key=AIzaSyCJIzfKoHlACqsmK1zDzl-OAsgtJPk8BtI');
        if (response.status === 200) {
            return response.json();
        }
    } catch (error) {
        console.error('Error fetching Google Fonts:', error);
    }
};

const TypographyNew = ({ setAttributes, attributes, fontSizeDefault = { unit: 'px', value: 16 }, spacingDefaultValue = { unit: 'px', value: 0 }, lineDefaultValue = { unit: 'px', value: 0 } }) => {
    const [typographyOpen, setTypographyOpen] = useState(false);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [allFonts, setAllFonts] = useState([]);
    const [fontLists, setFontLists] = useState([]);
    const onMenuOpen = () => setIsMenuOpen(true);
    const onMenuClose = () => setIsMenuOpen(false);
    const typoBtn = useRef();
    const {
        family,
        familyKey,
        fontSize,
        fontSizeKey,
        fontSpacing,
        fontSpacingKey,
        lineHeight,
        lineHeightKey,
    } = attributes;

    useEffect(() => {
        if (typographyOpen && allFonts.length === 0) {
            fetchFonts().then((data) => {
                let fonts = data.items.map((item) => {
                    return { label: item.family, value: { family: item.family, variants: item.variants } };
                })
                setAllFonts(fonts);
                setFontLists(fonts.filter((font, i) => (i < 50) && font));
            });
        }
    }, [typographyOpen])

    const fontSearch = (inputValue) => {
        let googleFonts = allFonts.filter((font) => {
            return font.label.toLowerCase().includes(inputValue.toLowerCase())
        }).filter((font, i) => (i < 30) && font);
        setFontLists(googleFonts)
    }

    const fontWCheck = Array('100italic', '300italic', 'italic', '400italic', '500italic', '600italic', '700italic', '800italic', '900italic');
    const fontW = family.googleFont?.variants?.map((value) => {
        if (fontWCheck.includes(value) || '100' === value) {
            return '';
        }

        if ('regular' === value) {
            value = '400';
        }
        return { label: value, value: value }
    });

    let fontWeightList = [];
    if (fontW) {
        fontWeightList = fontW.filter(function (item, pos) {
            return '' !== item;
        })
    }

    return (
        <>
            <div className='styble-typography styble-component-mb' onClick={(e) => (typographyOpen && !e.target.closest('.styble-typography-fonts')) && setTypographyOpen(false)}>
                <div ref={typoBtn} className={`styble-typography-btn ${typographyOpen && 'active'}`}>
                    <p className="styble-component-title">Typography</p>
                    <Button onClick={() => setTypographyOpen(!typographyOpen)}>{<TypographyIcon />}</Button>
                </div>

                <div className={`styble-typography-fonts ${typographyOpen ? 'open' : ''}`}>
                    <div className={`styble-typography-family styble-component-mb`}>
                        {/* {typographyOpen && <FontPicker
                            apiKey='AIzaSyCJIzfKoHlACqsmK1zDzl-OAsgtJPk8BtI'
                            activeFontFamily={family.googleFont?.family}
                            onChange={(nextFont) => (
                                setAttributes({ [familyKey]: { ...family, 'googleFont': nextFont, 'typography': { ...family.typography, 'family': nextFont?.family, 'fontWeight': ('regular' === nextFont?.variants[0]) ? '400' : nextFont?.variants[0] } } })
                            )}
                            limit={950}
                        />} */}
                        <Select
                            onMenuOpen={onMenuOpen}
                            onMenuClose={onMenuClose}
                            options={[{ label: 'Default', value: { family: 'Default', variants: ['regular'] } }
                                , ...fontLists]}
                            placeholder={family?.googleFont?.family}
                            onChange={(nextFont) => (
                                setAttributes({ [familyKey]: { ...family, 'googleFont': nextFont?.value, 'typography': { ...family.typography, 'family': ('Default' !== nextFont?.value?.family) ? nextFont?.value?.family : '', 'fontWeight': ('regular' === nextFont?.value?.variants[0]) ? '400' : nextFont?.value?.variants[0] } } })
                            )}
                            onInputChange={(inputValue) => fontSearch(inputValue)}
                        />

                    </div>
                    <StybleRanger
                        label={__('Font Size', 'styble')}
                        setAttributes={setAttributes}
                        attributes={fontSize}
                        units={['Px', '%', 'Em']}
                        attributesKey={fontSizeKey}
                        defaultValue={fontSizeDefault}
                    />
                    <SelectControl
                        label={__('Font Style', 'styble')}
                        value={family.typography.style}
                        options={[
                            // {label: 'Default', value: 'default'},
                            { label: 'Normal', value: 'normal' },
                            { label: 'Italic', value: 'italic' },
                            { label: 'Oblique', value: 'oblique' },
                        ]}
                        onChange={(newStyle) => setAttributes({ [familyKey]: { ...family, 'typography': { ...family.typography, 'style': newStyle } } })}
                        __nextHasNoMarginBottom
                    />
                    <SelectControl
                        label="Font Weight"
                        value={family.typography.fontWeight}
                        options={fontWeightList}
                        onChange={(newWeight) => setAttributes({ [familyKey]: { ...family, 'typography': { ...family.typography, 'fontWeight': newWeight } } })}
                        __nextHasNoMarginBottom
                    />
                    <StybleRanger
                        label={__('Letter Spacing', 'styble')}
                        setAttributes={setAttributes}
                        attributes={fontSpacing}
                        units={['Px', '%', 'Em']}
                        attributesKey={fontSpacingKey}
                        defaultValue={spacingDefaultValue}
                    />
                    <StybleRanger
                        label={__('Line Height', 'styble')}
                        setAttributes={setAttributes}
                        attributes={lineHeight}
                        units={['Px', '%', 'Em']}
                        attributesKey={lineHeightKey}
                        defaultValue={lineDefaultValue}
                    />
                    <StybleButtonGroup
                        label={__('Text Decoration', 'styble')}
                        attributes={family.typography.decoration}
                        items={[
                            { 'label': <DecorationNone />, 'value': 'none' },
                            { 'label': <DecorationUnderline />, 'value': 'underline' },
                            { 'label': <DecorationLineThrough />, 'value': 'line-through' },
                            { 'label': <DecorationOverLine />, 'value': 'overline' },
                        ]}
                        onClick={(newValue) => setAttributes({ [familyKey]: { ...family, 'typography': { ...family.typography, 'decoration': newValue } } })}
                    />
                    <StybleButtonGroup
                        label={__('Letter Case', 'styble')}
                        attributes={family.typography.transform}
                        items={[
                            { 'label': 'None', 'value': 'none' },
                            { 'label': 'AB', 'value': 'uppercase' },
                            { 'label': 'ab', 'value': 'lowercase' },
                            { 'label': 'Ab', 'value': 'capitalize' },
                        ]}
                        onClick={(newValue) => setAttributes({ [familyKey]: { ...family, 'typography': { ...family.typography, 'transform': newValue } } })}
                    />
                </div>
            </div>
        </>
    )
}

export default TypographyNew;