import { __ } from '@wordpress/i18n';
import {
	PanelBody,
    SelectControl
} from '@wordpress/components';
import {
    useState
} from '@wordpress/element';
import {
    useSelect,
} from '@wordpress/data';

import StybleRanger from '../../components/rangeControl/RangeControl';
import StybleButtonGroup from '../../components/buttonGroup/buttonGroup';
import Toggle from '../../components/toggle/toggle';
import {LeftIcon, RightIcon, CenterIcon} from '../../components/buttonGroup/svgIcon';
import { MultiSelect } from "react-multi-select-component";

const GeneralTab = ({attributes, setAttributes}) => {
    const [openTab, setOpenTab] = useState( 'general' );
    const {
        column,
        layout,
        gapBetweenItem,
        postPerPage,
        contentAlignment,
        enableImage,
        showTitle,
        showAuthor,
        showDate,
        showContent,
        showTaxonomy,
        showReadMore,
        titleTag,
        btnAlignment,
        showPagination,
        paginationAlignment,
        orderBy,
        order,
        cateList,
        imageSize,
        maxNumberOfWords,
        showSliderControls,
        sliderControlsAlignment,
        sliderControlsgap
    } = attributes;

    let categories = useSelect((select) => {
        return select('core').getEntityRecords('taxonomy', 'category', {'per_page': -1})?.map((item) => {
            return {label: item.name, value: item.id}
        })
    }, [])

    return(
        <>
        {/* General panelBody */}
        <PanelBody
            opened={('general' === openTab) ? true : false}
            onToggle={() => ('general' === openTab) ? setOpenTab('') : setOpenTab('general')}
            title={__('General', 'styble')}
        >
            {/* <StybleButtonGroup 
                label={__('Layout', 'styble')}
                attributes={layout}
                setAttributes={setAttributes}
                attributesKey={'layout'}
                items={[
                    {'label': 'Grid', 'value': 'grid'},
                    {'label': 'Masonry', 'value': 'masonry'},
                ]}
            /> */}
            <StybleRanger 
                label={__('Column', 'styble')}
                setAttributes={setAttributes}
                attributes={column}
                units= {false}
                attributesKey={'column'}
                defaultValue={{unit: 'px', value: 4}}
                max='5'
                min='1'
            />
            <StybleRanger 
                label={__('Column Gap', 'styble')}
                setAttributes={setAttributes}
                attributes={gapBetweenItem}
                units= {['Px', '%', 'Em']}
                attributesKey={'gapBetweenItem'}
                defaultValue={{unit: 'px', value: 20}}
                max='50'
            />
            <StybleRanger 
                label={__('Post Per Page', 'styble')}
                setAttributes={setAttributes}
                attributes={postPerPage}
                units= {false}
                attributesKey={'postPerPage'}
                defaultValue={{unit: 'px', value: 8}}
                max={50}
            />
            <div className='styble-post-editor-cats'>
                <label className="styble-component-title">Categories</label>
                <MultiSelect
                    options={(categories) ? categories : []}
                    value={cateList}
                    hasSelectAll={false}
                    onChange={(newValue) => setAttributes({cateList: newValue})}
                    labelledBy="Select Categories"
                />
                <br />
            </div>
            <SelectControl
                label={__('Order By', 'styble')}
                value={ orderBy }
                options={[
                    {'label': 'Date', 'value': 'date'},
                    {'label': 'Title', 'value': 'title'},
                    {'label': 'Random', 'value': 'rand'},
                    {'label': 'Menu Order', 'value': 'menu_order'},
                ]}
                onChange={ ( newValue ) => setAttributes({ orderBy: newValue }) }
                __nextHasNoMarginBottom
            />
            {('menu_order' !== orderBy) && <StybleButtonGroup 
                label={__('Order', 'styble')}
                attributes={order}
                setAttributes={setAttributes}
                attributesKey={'order'}
                items={[
                    {'label': 'Descending', 'value': 'desc'},
                    {'label': 'Ascending', 'value': 'asc'},
                ]}
            />}
        </PanelBody>
        <PanelBody
            title={__('Image', 'styble')}
            opened={('image' === openTab) ? true : false}
            onToggle={() => ('image' === openTab) ? setOpenTab('') : setOpenTab('image')}
        >
            <Toggle 
                label = {__( 'Featured Image', 'styble' )}
                attributes= { enableImage }
                attributesKey= { 'enableImage' }
                setAttributes= { setAttributes }
            />
            {enableImage && <SelectControl
                label={__('Image Size', 'styble')}
                value={ imageSize }
                options={stybleLocalize.image_size}
                onChange={ ( newValue ) => setAttributes({ imageSize: newValue }) }
                __nextHasNoMarginBottom
            />}
        </PanelBody>
        <PanelBody
            title={__('Content', 'styble')}
            opened={('content' === openTab || 'contentTitle' === openTab || 'contentMeta' === openTab || 'contentDes' === openTab) ? true : false}
            onToggle={() => ('content' === openTab) ? setOpenTab('') : setOpenTab('content')}
        >
            <StybleButtonGroup 
                label={__('Alignment', 'styble')}
                attributes={contentAlignment}
                attributesKey={'contentAlignment'}
                setAttributes={setAttributes}
                border={false}
                items={[
                    {'label': <LeftIcon />, 'value': 'left'},
                    {'label': <CenterIcon />, 'value': 'center'},
                    {'label': <RightIcon />, 'value': 'right'},
                ]}
            />

            <PanelBody
                title={__('Title', 'styble')}
                opened={('contentTitle' === openTab) ? true : false}
                onToggle={() => ('contentTitle' === openTab) ? setOpenTab('content') : setOpenTab('contentTitle')}
            >
                <Toggle 
                    label = {__( 'Enable Title', 'styble' )}
                    attributes= { showTitle }
                    attributesKey= { 'showTitle' }
                    setAttributes= { setAttributes }
                />
                {showTitle && <StybleButtonGroup 
                    label={__('Title Tag', 'styble')}
                    attributes={titleTag}
                    attributesKey={'titleTag'}
                    setAttributes={setAttributes}
                    border={false}
                    items={[
                        {'label': 'H1', 'value': 'h1'},
                        {'label': 'H2', 'value': 'h2'},
                        {'label': 'H3', 'value': 'h3'},
                        {'label': 'H4', 'value': 'h4'},
                        {'label': 'H5', 'value': 'h5'},
                        {'label': 'H6', 'value': 'h6'},
                        {'label': 'P', 'value': 'p'},
                    ]}
                />}
            </PanelBody>
            <PanelBody
                title={__('Meta', 'styble')}
                opened={('contentMeta' === openTab) ? true : false}
                onToggle={() => ('contentMeta' === openTab) ? setOpenTab('content') : setOpenTab('contentMeta')}
            >
                <Toggle 
                    label = {__( 'Author', 'styble' )}
                    attributes= { showAuthor }
                    attributesKey= { 'showAuthor' }
                    setAttributes= { setAttributes }
                />
                <Toggle 
                    label = {__( 'Date', 'styble' )}
                    attributes= { showDate }
                    attributesKey= { 'showDate' }
                    setAttributes= { setAttributes }
                />
                <Toggle 
                    label = {__( 'Taxonomy', 'styble' )}
                    attributes= { showTaxonomy }
                    attributesKey= { 'showTaxonomy' }
                    setAttributes= { setAttributes }
                />
            </PanelBody>
            
            <PanelBody
                title={__('Description', 'styble')}
                opened={('contentDes' === openTab) ? true : false}
                onToggle={() => ('contentDes' === openTab) ? setOpenTab('content') : setOpenTab('contentDes')}
            >
                <Toggle 
                    label = {__( 'excerpt', 'styble' )}
                    attributes= { showContent }
                    attributesKey= { 'showContent' }
                    setAttributes= { setAttributes }
                />
                <StybleRanger 
                    label={__('Max Number of Words', 'styble')}
                    setAttributes={setAttributes}
                    attributes={maxNumberOfWords}
                    units= {false}
                    attributesKey={'maxNumberOfWords'}
                    defaultValue={{unit: 'px', value: 55}}
                    max={200}
                />
            </PanelBody>
        </PanelBody>
        <PanelBody
            title={__('Action Button', 'styble')}
            opened={('actionBtn' === openTab) ? true : false}
            onToggle={() => ('actionBtn' === openTab) ? setOpenTab('') : setOpenTab('actionBtn')}
        >
            <Toggle 
                label = {__( 'Read More', 'styble' )}
                attributes= { showReadMore }
                attributesKey= { 'showReadMore' }
                setAttributes= { setAttributes }
            />
            {showReadMore && <StybleButtonGroup 
                label={__('Alignment', 'styble')}
                attributes={btnAlignment}
                attributesKey={'btnAlignment'}
                setAttributes={setAttributes}
                border={false}
                items={[
                    {'label': <LeftIcon />, 'value': 'left'},
                    {'label': <CenterIcon />, 'value': 'center'},
                    {'label': <RightIcon />, 'value': 'right'},
                ]}
            />}
        </PanelBody>
        <PanelBody
            title={__('Pagination', 'styble')}
            opened={('pagination' === openTab) ? true : false}
            onToggle={() => ('pagination' === openTab) ? setOpenTab('') : setOpenTab('pagination')}
        >
            <Toggle 
                label = {__( 'Pagination', 'styble' )}
                attributes= { showPagination }
                attributesKey= { 'showPagination' }
                setAttributes= { setAttributes }
            />
            {showPagination && <StybleButtonGroup 
                label={__('Alignment', 'styble')}
                attributes={paginationAlignment}
                attributesKey={'paginationAlignment'}
                setAttributes={setAttributes}
                border={false}
                items={[
                    {'label': <LeftIcon />, 'value': 'left'},
                    {'label': <CenterIcon />, 'value': 'center'},
                    {'label': <RightIcon />, 'value': 'right'},
                ]}
            />}
        </PanelBody>
        <PanelBody
            title={__('Slider Controls', 'styble')}
            opened={('slider-controls' === openTab) ? true : false}
            onToggle={() => ('slider-controls' === openTab) ? setOpenTab('') : setOpenTab('slider-controls')}
        >
            <Toggle 
                label = {__( 'Slider Controls', 'styble' )}
                attributes= { showSliderControls }
                attributesKey= { 'showSliderControls' }
                setAttributes= { setAttributes }
            />
            {showSliderControls && <StybleButtonGroup 
                label={__('Alignment', 'styble')}
                attributes={sliderControlsAlignment}
                attributesKey={'sliderControlsAlignment'}
                setAttributes={setAttributes}
                border={false}
                items={[
                    {'label': <LeftIcon />, 'value': 'left'},
                    {'label': <CenterIcon />, 'value': 'center'},
                    {'label': <RightIcon />, 'value': 'right'},
                ]}
            />}
        </PanelBody>
        </>
    )
}

export default GeneralTab;