import { __ } from '@wordpress/i18n';
import Spacing from "../spacing/spacing";
import { useState } from '@wordpress/element';
import StybleButtonGroup from '../buttonGroup/buttonGroup';
import StybleColorPicker from '../color/color';
import StybleRanger from '../rangeControl/RangeControl';

const BoxShadow = ({attributes, attributesKey, setAttributes, transition}) => {

    const [ buttonTab, setButtonTab ] = useState('default');

    const shadowColor = (newColor) => {
        setAttributes({[attributesKey]: {...attributes, 'color': newColor}})
    }

    const shadowHoverColor = (newColor) => {
        setAttributes({[attributesKey]: {...attributes, 'hoverColor': newColor}})
    }

    const tabButton = ( newValue ) => {
        setButtonTab( newValue )
    }

    return(
        <>
            <Spacing
                label={__('Box Shadow', 'styble')}
                attributes={attributes}
                attributesKey={attributesKey}
                setAttributes={setAttributes}
                boxUnits={true}
                defaultValue={{'unit': 'Outset', 'value': {
                    'top': "0",
                    'right': "3",
                    'bottom': "6",
                    'left': "0",
                }}}
                linkButton={false}
                units={["Outset", 'Inset']}
                labelItem={{
                    'top': __('X Offset', 'styble'),
                    'right': __('Y Offset', 'styble'),
                    'bottom': __('Blur', 'styble'),
                    'left': __('Speared', 'styble')
                }}
            />
            {/* Shadow Color */}
            <StybleButtonGroup 
                label={__('Shadow Color', 'styble')}
                attributes={buttonTab}
                items={[
                    {'label': 'Default', 'value': 'default'},
                    {'label': 'Hover', 'value': 'hover'},
                ]}
                onClick={tabButton}
            />

            {'default' === buttonTab && <StybleColorPicker
                label={__('Color', 'styble')}
                value={attributes.color}
                onChange={shadowColor}
            />}
            {'hover' === buttonTab && <><StybleColorPicker
                label={__('Hover Color', 'styble')}
                value={attributes.hoverColor}
                onChange={shadowHoverColor}
            />
            {transition && <StybleRanger 
                label={__('Transition', 'styble')}
                setAttributes={setAttributes}
                attributes={transition.value}
                units={false}
                step={0.1}
                min={0}
                max={30}
                defaultValue={{unit: 'px', value: 0.3}}
                attributesKey={transition.key}
            />}
            </>}
        </>
    )
}

export default BoxShadow;